ss it. It creates a vibrant type of motion illusion with revolving wheels, billowing smoke, ripples in water, etc.")]),_c('b-col',[_c('b-img',{staticClass:"mt-4",attrs:{"thumbnail":"","src":"http://tile.loc.gov/storage-services/service/pnp/ppmsca/05900/05954/05954v.jpg"}})],1),_c('br'),_c('li',[_vm._v("Auguste Berthier's autostereograms")]),_c('p',[_vm._v("In May 1896 Auguste Berthier published an article describing his method of creating an autostereogram. Alternating strips from the left and right image of a traditional stereoscopic negative had to be recomposed as an interlaced image, preferably during the printing of the image on paper. A glass plate with opaque lines had to be fixed in front of the interlaced print with a few millimeters in between, so the lines on the screen formed a parallax barrier: from the right distance and angle each eye could only see the photographic strips shot from the corresponding angle.")]),_c('b-col',[_c('b-img',{staticClass:"mt-4",attrs:{"thumbnail":"","src":"https://jiupinjia.github.io/neuralmagiceye/rst.jpg"}})],1),_c('br'),_c('li',[_vm._v("Frederic Ives' parallax stereogram "),_c('span',[_vm._v("&")]),_vm._v(" changeable picture")]),_c('p',[_vm._v("On December 5, 1901 American inventor Frederic Eugene Ives presented his \"parallax stereogram\" at the Franklin Institute of the State of Pennsylvania. The \"parallax stereogram\" was a photo shot through two apertures behind the lens with a \"transparent-line screen, consisting of opaque lines with clear spaces between them\" in front of the sensitive plate, slightly separated from it. The line screen had 200 parallel lines per inch (79/cm) and was contact-printed from an original factory halftone screen. The technique received U.S. patent on April 14, 1903")]),_c('b-col',[_c('b-img',{staticClass:"mt-4",attrs:{"thumbnail":"","src":"https://www.researchgate.net/profile/Lois-Mignard-Debise/publication/324525451/figure/fig4/AS:615507233353733@1523759768125/Patent-sketch-for-the-parallax-stereogram-making-camera-from-Ives-1903.png"}})],1),_c('br'),_c('li',[_vm._v("Eugene Estanave's animated autostereograms")]),_c('p',[_vm._v("French mathematician Eugène Estanave investigated the parallax stereogram and started working with the technique late in 1905. On January 24, 1906, Estanave filed for French patent for a stereophotography device and stereoscopy using line sheets. It included his \"changing\" pictures that applied the principle of Ives' \"Changeable sign\" to animated photography, for instance the portrait of a woman with eyes open or closed depending on the viewing angle.")]),_c('li',[_vm._v("Magic moving picture cards")]),_c('p',[_vm._v("Magic moving pictures were composed of images containing black vertical and regularly interlaced stripes, alternating between two or three phases of a depicted motion or between distinctly different pictures. A little transparent sheet with regular vertical black stripes was glued beneath a window in a cardboard envelope holding the picture card. The card was pulled out and pushed back in to produce the illusion of change or motion.")]),_c('b-col',[_c('b-img',{staticClass:"mt-4",attrs:{"thumbnail":"","src":"https://upload.wikimedia.org/wikipedia/commons/thumb/0/0e/Postcard-chicago-magic-moving-pictures-nice-19061.jpg/640px-Postcard-chicago-magic-moving-pictures-nice-19061.jpg"}})],1),_c('br'),_c('li',[_vm._v("Ombro-Cinema")]),_c('p',[_vm._v("Ombro-Cinéma toys operated on rotating scrolls of paper with sequences of images printed as interlaced two-frame animations: thin regularly-spaced vertical stripes of one frame of the animation were alternated with stripes of the next frame, alternately hidden by regularly-spaced black vertical stripes on a transparent viewing pane. In some versions the stripes on the viewing pane were disguised as a picket fence. Ombro-Cinema toys had of a wooden or cardboard chassis with a rack and hand-crank for cycling the image scroll across the viewing pane. In some versions a wind-up clockwork mechanism transferred the scroll while operating a music box.")]),_c('b-img',{attrs:{"thumbnail":"","src":"https://upload.wikimedia.org/wikipedia/commons/thumb/2/22/1921_ombro-cin%C3%A9ma.jpg/1280px-1921_ombro-cin%C3%A9ma.jpg"}}),_c('li',[_vm._v("Autostereoscopic cinema")]),_c('p',[_vm._v("Autostereoscopy is any method of displaying stereoscopic images (adding binocular perception of 3D depth) without the use of special headgear, glasses, something that affects vision, or anything for eyes on the part of the viewer. Because headgear is not required, it is also called \"glasses-free 3D\" or \"glassesless 3D\"")]),_c('li',[_vm._v("Kinegram")]),_c('p',[_vm._v("Visual artist Gianni A. Sarcone has produced animations that he calls kinegrams since 1997. He describes his animations as \"optic kinetic media\" that \"artfully combine the visual effects of moiré patterns with the zoetrope animation technique\". Sarcone also created rotating animations that use a transparent disc with radial lines that has to be spun around its center to animate the picture.")]),_c('li',[_vm._v("Scanimation")]),_c('p',[_vm._v("\"Scanimation\", incorporating sliding striped acetate sheets into book pages or folding cards to produce barrier-grid animations of six phases or more at each page, was produced by Rufus Butler Seder starting in 2007.")])],1)])],1),_c('b-row',[_c('p')]),_c('b-row',[_c('h2',[_vm._v("Methods")])]),_c('b-row',[_c('b-col',[_c('p',[_vm._v("The process of creating this ilussion can be splited in the next steps:")]),_c('ol',[_c('li',[_vm._v("Decide what to animate. It may be an abstract pattern, a cartoon, or a "),_c('u',[_vm._v("sequence of photos")]),_vm._v(". Here are some hints about which kind of images work best.")]),_c('ul',[_c('li',[_vm._v("It's best to use bright, bold, high-contrast images with no small details. This is because only thin slivers of the image are visible through the striped overlay.")]),_c('li',[_vm._v("It's best if the animation is cyclic, beginning and ending at the same position.")]),_c('li',[_vm._v("It's best if the animation makes sense both forward and backward as you move the overlay back and forth.")]),_c('li',[_vm._v("It's best if no part of the image moves too much between frames.")]),_c('li',[_vm._v("The easiest animations are made of rigid shapes that merely move, rotate, expand/contract, or change colors.")])]),_c('li',[_vm._v("Set the parameters.")]),_c('ul',[_c('li',[_vm._v("Decide on the animation cycle length.")]),_c('li',[_vm._v("Decide on the grid unit size.")]),_c('li',[_vm._v("Decide on the overall size of the images.")]),_c('li',[_vm._v("Decide whether the stripes will be horizontal or vertical.")])]),_c('li',[_vm._v("Make the animation. Make one picture for each frame of the animation. For example, if you want an animation of a spoked wheel turning, make pictures of the wheel at different angles for each frame. If you want an animation of an animal running, make pictures of the animal at different points in its stride.")]),_c('li',[_vm._v("Interlace the frames, it can be made using a software like p5.js")]),_c('li',[_vm._v("View the kinegram by moving the overlay over the generated picture.")])])])],1),_c('b-row',[_c('h2',[_vm._v("Code and Results")])]),_c('b-row',[_c('SketchP5',{staticClass:"mt-4",attrs:{"sketch":_vm.sketch,"width":960,"heigth":620}})],1),_c('div',[_c('code-highlight',{attrs:{"language":"javascript"}},[_c('pre',[_vm._v("                /**\n                * Application of Scanimation visual illusion using p5.js\n                * @param {p5} p5 reference \n                */\n\n                const sketch = (p5) => {\n                    let canvas;\n                    let input;\n                    let button;\n                    let clear;\n                    let check;\n                    let image;\n                    let frames = [];\n                    let x = 0;\n\n                    p5.setup = () => {\n                        canvas = p5.createCanvas(960, 540);\n                        canvas.parent(\"vue-canvas\");\n\n                        input = p5.createFileInput(handleFileLoad, true);\n                        input.parent(\"vue-canvas\");\n\n                        button = p5.createButton('Generate');\n                        button.parent(\"vue-canvas\");\n                        button.mousePressed(handleGenerateButtonClick);\n\n                        clear = p5.createButton('Clear');\n                        clear.parent(\"vue-canvas\");\n                        clear.mousePressed(handleClearButtonClick);\n\n                        check = p5.createCheckbox('Overlay', false);\n                        check.parent(\"vue-canvas\");\n                        check.changed(handleChangeCheckbox);\n\n                        p5.background(\"#1e1e1e\");\n                    }\n\n                    p5.draw = () => {\n                        if(check.checked()){\n                            x = (x + 2) % (2 * frames.length);\n                            drawGeneratedImage();\n                            p5.fill(p5.color(0));\n                            for (let i = - 2 * frames.length; i <= image.width; i += 2 * frames.length) {\n                                p5.rect(i + x, 0, 2 * (frames.length - 1), image.height);\n                            }\n                        }\n                    }\n\n                    const handleFileLoad = (file) => {\n                        if (file.type === 'image') {\n                            frames.push(p5.loadImage(file.data));\n                        }\n                    }\n\n                    const handleGenerateButtonClick = () => {\n                        if(frames.length > 0){\n                            drawGeneratedImage();\n                        }\n                    }\n\n                    const handleClearButtonClick = () => {\n                        p5.background(\"#1e1e1e\");\n                        frames = [];\n                    }\n\n                    const handleChangeCheckbox = () => {\n                        if(!check.checked()){\n                            drawGeneratedImage();  \n                        }\n                    }\n\n                    const drawGeneratedImage = () => {\n                        image = p5.createImage(960, 540);\n                        for (let i = 0; i <= image.width; i += 2) {\n                            image.set(i, 0, frames[i / 2 % frames.length].get(i, 0, 2, image.height));\n                        }\n                        p5.image(image, 0, 0);\n                    }\n                }\n\n                export default sketch;\n            ")])])],1),_c('b-row',[_c('h2',[_vm._v("Discussion")])]),_c('b-row',[_c('p',[_vm._v("To improve the current implementation of scanimation, we can use as input a video, We can take some frames of it to generate the scanimation picture. Also we can construct the overlay in vertical way, not only horizontal.")])]),_c('b-row',[_c('h2',[_vm._v("Conclusion")])]),_c('b-row',[_c('p',[_vm._v("Scanimation is a good way to create simple animations using static images, the film industry had started using this principle of create the first movies, the praxinoscope has used blinking light to simulate the scanimation overlay and an a sequence of frames.")])])],1)}
